import React from "react";
import "./projects.scss";
import Typography from "@material-ui/core/Typography";
import LaunchIcon from "@material-ui/icons/Launch";
import { useContentfulData } from "../../Content/useContentfulData";
import LoadingData from "../../Assets/Loading.jsx";

const Projects = () => {
  const [projectpageData, loadingprojectpageData] = useContentfulData(
    "projectpage"
  );
  // const [projectPageData, loadingprojectPageData] = useContentfulData("projectPage");

  if (loadingprojectpageData) {
    return <LoadingData />;
  }

  return (
    <div className=" project-section" id="projects">
      <header
        className="App-header container padding-50 xheight-100 xsection-height xvertical-center"
        id="projects"
      >
        <div className="heading-behind ">project</div>

        <Typography variant="h2" className="heading">
          Project
        </Typography>

        {projectpageData.items.map((projectpage) => {
          // console.log(projectpage);
          for (var i = 0; i < projectpage.fields.project.length; i++) {
            // var theproject = projectpage.fields.project[i];
            // console.log(theproject);

            // projectpage.fields.project.forEach(function (item, index, array) {
            //   console.log(item, index);
            // });

            return (
              <Typography variant="h3" className="about-text">
                <div className="content">
                  <Typography className="about-text">
                    {projectpage.fields.description}
                  </Typography>


                  <div className=" project-accomplishment">
                    {projectpage.fields.project.map((project) => (
                      <div className="project-card">
                        <div
                          className="thumb"
                          style={{
                            background: `url('${
                              "https:" + project.fields.image.fields.file.url
                            }')`,
                            backgroundSize: `cover`,
                          }}
                        ></div>
                        <div className="infos">
                          <a
                            href={project.fields.linkText}
                            className="flag"
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            <LaunchIcon />
                          </a>

                          <Typography variant="h4" className="category">
                            {project.fields.category}
                          </Typography>
                          <Typography variant="h4" className="title">
                            {project.fields.title}
                          </Typography>
                          <Typography variant="h4" className="description">
                            {
                              project.fields.description.content[0].content[0]
                                .value
                            }
                          </Typography>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </Typography>
            );
          }
        })}
      </header>
    </div>
  );
};

export default Projects;
